import React from 'react';
import { motion } from 'framer-motion';
import { useNavigate } from 'react-router-dom';
import { App } from '../types';
import { useStore } from '../store';

interface CatalogCardProps {
  item: App;
}

export const CatalogCard: React.FC<CatalogCardProps> = ({ item }) => {
  const navigate = useNavigate();
  const { favorites, toggleFavorite } = useStore();

  const isFavorite = favorites.includes(item.id);

  const handleFavoriteClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    toggleFavorite(item.id);
  };

  const handleCardClick = () => {
    navigate(`/app/${item.id}`);
  };

  const handleTelegramClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    window.open(item.telegramLink, '_blank');
  };

  return (
    <div className="bg-telegram-bg-secondary rounded-lg p-4 shadow-sm hover:shadow-md transition-shadow">
      <div className="flex items-start space-x-4">
        <div className="flex-shrink-0 w-12 h-12 flex items-center justify-center bg-telegram-primary rounded-lg text-2xl">
          {item.icon}
        </div>
        <div className="flex-1 min-w-0">
          <h3 className="text-lg font-medium text-telegram-text-primary truncate">
            {item.name}
          </h3>
          <p className="text-sm text-telegram-text-secondary line-clamp-2">
            {item.description}
          </p>
          <div className="mt-2 flex flex-wrap gap-2">
            {item.tags.map((tag) => (
              <span
                key={tag}
                className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-telegram-primary/10 text-telegram-primary"
              >
                {tag}
              </span>
            ))}
          </div>
        </div>
      </div>
      <div className="mt-4">
        <button
          onClick={handleTelegramClick}
          className="w-full py-2 px-4 bg-telegram-primary text-telegram-text-light rounded-lg hover:bg-telegram-dark transition-colors"
        >
          Открыть
        </button>
      </div>
    </div>
  );
}; 