import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { collection, getDocs, addDoc } from 'firebase/firestore';
import { db } from '../firebase/config';
import { App } from '../types';
import { mockItems } from '../data/mockData';
import { CatalogCard } from '../components/CatalogCard';

const categories = [
  {
    id: 'all',
    name: 'Все',
    icon: '📱'
  },
  {
    id: 'bots',
    name: 'Боты',
    icon: '🤖'
  },
  {
    id: 'channels',
    name: 'Каналы',
    icon: '📢'
  },
  {
    id: 'apps',
    name: 'Приложения',
    icon: '📲'
  }
];

const Home: React.FC = () => {
  const [apps, setApps] = useState<App[]>([]);
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [selectedApp, setSelectedApp] = useState<App | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchApps = async () => {
      try {
        const appsCollection = collection(db, 'apps');
        const appsSnapshot = await getDocs(appsCollection);
        const appsData = appsSnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        })) as App[];

        if (appsData.length === 0) {
          // Если коллекция пуста, используем моковые данные
          setApps(mockItems);
        } else {
          setApps(appsData);
        }
      } catch (error) {
        console.error('Error fetching apps:', error);
        // В случае ошибки используем моковые данные
        setApps(mockItems);
      } finally {
        setIsLoading(false);
      }
    };

    fetchApps();
  }, []);

  const filteredApps = apps.filter(app => 
    selectedCategory === 'all' ? true : app.category === selectedCategory
  );

  const handleAppClick = (app: App) => {
    setSelectedApp(app);
  };

  const handleBackClick = () => {
    setSelectedApp(null);
  };

  if (selectedApp) {
    return (
      <div className="min-h-screen bg-gray-100 p-2">
        <div className="max-w-[100vw] mx-auto">
          <button
            onClick={handleBackClick}
            className="mb-2 px-3 py-1.5 bg-indigo-600 text-white rounded-md hover:bg-indigo-700 text-sm"
          >
            ← Назад
          </button>
          <div className="bg-white rounded-lg shadow-md p-3">
            <div className="flex items-center mb-3">
              <span className="text-4xl mr-3">{selectedApp.icon}</span>
              <div>
                <h2 className="text-lg font-bold">{selectedApp.name}</h2>
                <p className="text-sm text-gray-600">{selectedApp.description}</p>
              </div>
            </div>
            <div className="mb-3">
              <h3 className="text-base font-semibold mb-1">Особенности:</h3>
              <ul className="list-disc list-inside text-sm">
                {selectedApp.features.map((feature, index) => (
                  <li key={index} className="text-gray-700">{feature}</li>
                ))}
              </ul>
            </div>
            <div className="mb-3">
              <h3 className="text-base font-semibold mb-1">Теги:</h3>
              <div className="flex flex-wrap gap-1">
                {selectedApp.tags.map((tag, index) => (
                  <span
                    key={index}
                    className="px-2 py-0.5 bg-gray-200 rounded-full text-xs"
                  >
                    {tag}
                  </span>
                ))}
              </div>
            </div>
            <a
              href={selectedApp.telegramLink}
              target="_blank"
              rel="noopener noreferrer"
              className="inline-block w-full text-center px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-700 text-sm"
            >
              Открыть в Telegram
            </a>
          </div>
        </div>
      </div>
    );
  }

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-telegram-primary"></div>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-6">
      <div className="flex overflow-x-auto pb-4 mb-6 -mx-4 px-4 scrollbar-hide">
        <div className="flex space-x-2">
          {categories.map(category => (
            <button
              key={category.id}
              onClick={() => setSelectedCategory(category.id)}
              className={`flex items-center px-4 py-2 rounded-full whitespace-nowrap transition-colors ${
                selectedCategory === category.id
                  ? 'bg-telegram-primary text-telegram-text-light'
                  : 'bg-telegram-bg-secondary text-telegram-text-primary'
              }`}
            >
              <span className="mr-2">{category.icon}</span>
              {category.name}
            </button>
          ))}
        </div>
      </div>

      <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 md:grid-cols-3">
        {filteredApps.map(app => (
          <CatalogCard key={app.id} item={app} />
        ))}
      </div>
    </div>
  );
};

export default Home; 